'use client';
import React from 'react';
import { getClients } from '@/services/clients';
import { Typography } from '@mui/material';
import { ClientWizard } from '@/features/clients/ClientWizard';

export default function ClientEditPage({ params }: { params: { id: string } }) {
  const initialClient = React.useMemo(() => {
    if (typeof window === 'undefined') return null;
    try {
      const raw = window.localStorage.getItem('clients_data');
      if (!raw) return null;
      const list = JSON.parse(raw) as any[];
      return list.find((c) => c.id === params.id) ?? null;
    } catch {
      return null;
    }
  }, [params.id]);

  const [client, setClient] = React.useState<any | null>(initialClient);

  React.useEffect(() => {
    let mounted = true;
    (async () => {
      const all = await getClients();
      const hit = all.find((c: any) => c.id === params.id);
      if (mounted) setClient(hit ?? null);
    })();
    return () => {
      mounted = false;
    };
  }, [params.id]);

  const base = client ?? { id: params.id, email: '', firstName: '', lastName: '', sport: '', agencyEmail: '' };
  return (
    <>
      <Typography variant="h4" gutterBottom>Edit Athlete</Typography>
      <ClientWizard initialClient={base} mode="edit" />
    </>
  );
}


